<template>
  <div>
    <NarBar/>
    <div class="banner"></div>
    <div class="containers mr-tt">
      <a-row class="items" :gutter="24">
        <a-col :span="16">
          <a-card class="bread card">
            <a-breadcrumb>
              <a-breadcrumb-item><a href="/">首页</a></a-breadcrumb-item>
              <a-breadcrumb-item>友链</a-breadcrumb-item>
            </a-breadcrumb>
          </a-card>
          <div class="mr-t links">
            <a-card class="card" v-for="item in links" :key="item.id" hoverable style="width: 220px">
              <img
                slot="cover"
                :alt="item.descption"
                width="200"
                :src="item.logo"
              />
              <a-card-meta :title="item.name">
                <template slot="description">
                  {{item.descption}}
                </template>
              </a-card-meta>
            </a-card>
          </div>
        </a-col>
        <a-col class="right-items" :span="8">
          <Profile/>
          <Category/>
          <Tag/>
        </a-col>
      </a-row>
    </div>
    <Footer/>
    <BackToTop/>
  </div>
</template>

<script>
  import NarBar from "./components/NarBar";
  import Profile from "./components/Profile";
  import Category from "./components/Category";
  import Tag from './components/Tag'
  import LinksApi from "../../api/Links";
  import Footer from "./components/Footer";
  import BackToTop from "./components/BackToTop";
  export default {
    name: "Links",
    components: {
      BackToTop,
      Footer,
      NarBar,
      Profile,
      Category,
      Tag
    },
    data() {
      return {
        links: []
      }
    },
    created () {
      window.document.title = '友链 - ETERNITY'
      this.loadLinks()
    },
    methods: {
      async loadLinks() {
        LinksApi.All().then(res => {
          if (res.code === 1000) {
            this.links = res.data.links
          } else {
            this.$message.error('加载友链列表失败')
          }
        })
      }
    }
  }
</script>

<style lang="less" scoped>
  .banner {
    position: relative;
    width: 100%;
    height: 500px;
    background: url("https://cdn.jsdelivr.net/gh/Bursteretion/file-repo/imgs/wallhaven-kw5pvq.png");
    background-size: 100% 100%;
  }
  .containers {
    padding-right: 15px;
    margin-bottom: 30px;
  }
  .mr-t {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  .card {
    overflow: hidden;
  }
  .links > * {
    margin-top: 10px;
  }
  .links > *:first-child,
  .links > *:nth-child(2),
  .links > *:nth-child(3) {
    margin-top: 0;
  }
  .right-items > *:not(:first-child) {
    margin-top: 10px;
  }
  @media screen and (max-width: 800px) {
    .links > * {
      width: 49% !important;
      margin-top: 10px;
    }
    .links > *:first-child,
    .links > *:nth-child(2) {
      margin-top: 0;
    }
    .containers {
      width: 95%;
      padding-right: 0 !important;
      margin-top: 40px;
    }
    .items > * {
      padding-left: 12px;
      padding-right: 12px;
      width: 100%;
    }
    .items > *:last-child {
      margin-top: 10px;
    }
    .banner {
      height: 300px;
    }
  }
</style>